let kanji = [];

function Kanji(kkanji, english, onyomi, kunyomi, words) {
	this.kanji = kkanji;
	this.english = english;
	this.onyomi = onyomi;
	this.kunyomi = kunyomi;
	this.words = words;
}
function Word(word, meaning, hiragana) {
	this.word = word;
	this.meaning = meaning;
	this.hiragana = hiragana;
}
